// export default PatientDetail;


import React, { useState, useMemo } from 'react';
import { useParams, Link } from 'react-router-dom';
import { InferenceClient } from "@huggingface/inference";

const PatientDetail = ({ patients }) => {
  const { id } = useParams();
  const [activeTab, setActiveTab] = useState('overview');
  const [isGeneratingSummary, setIsGeneratingSummary] = useState(false);
  const [aiSummary, setAiSummary] = useState('');
  const [showAiSummary, setShowAiSummary] = useState(false);
  
  // Convert the string ID to a number
  const patientId = parseInt(id, 10);
  
  // Find the patient with numeric ID
  const patient = patients.find(p => p.id === patientId);

  // Initialize Hugging Face client
  const client = new InferenceClient('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');

  // Helper functions - MOVED ABOVE useMemo
  const getMedicationStatus = (medication) => {
    if (medication.includes('active-')) return { status: 'Active', color: 'green' };
    if (medication.includes('completed-')) return { status: 'Completed', color: 'blue' };
    if (medication.includes('previous-use-')) return { status: 'Previous', color: 'gray' };
    return { status: 'Unknown', color: 'yellow' };
  };

  const getConditionSeverity = (condition) => {
    const severeConditions = ['coronary-syndrome', 'lymphoma', 'bipolar'];
    const moderateConditions = ['fever', 'aci'];
    if (severeConditions.includes(condition)) return { severity: 'Severe', color: 'red' };
    if (moderateConditions.includes(condition)) return { severity: 'Moderate', color: 'orange' };
    return { severity: 'Mild', color: 'green' };
  };

  // Calculate age from DOB
  const calculateAge = (dob) => {
    const birthDate = new Date(dob);
    const today = new Date();
    let age = today.getFullYear() - birthDate.getFullYear();
    const monthDiff = today.getMonth() - birthDate.getMonth();
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birthDate.getDate())) {
      age--;
    }
    return age;
  };

  // AI Summary Generation
  const generatePatientSummary = async () => {
    setIsGeneratingSummary(true);
    setShowAiSummary(true);
    
    try {
      const prompt = `
        Please provide a concise medical summary for this patient:
        
        Patient: ${patient.name}
        Gender: ${patient.Gender}
        Age: ${age} years
        Date of Birth: ${patient['D.O.B']}
        
        Medical Conditions: ${patient.conditions?.join(', ') || 'None'}
        Allergies: ${patient.allergies?.join(', ') || 'None known'}
        Current Medications: ${patient.medications?.filter(med => med.includes('active-')).map(med => med.replace('active-', '')).join(', ') || 'None'}
        Previous Medications: ${patient.medications?.filter(med => med.includes('completed-') || med.includes('previous-use-')).map(med => med.replace(/(completed-|previous-use-)/, '')).join(', ') || 'None'}
        
        Recent Clinical Observations: 
        ${patient.observations?.slice(0, 10).map(obs => 
          `- ${obs.type}: ${obs.value} ${obs.unit} (Reference: ${obs.reference_range}) on ${obs.date}`
        ).join('\n') || 'None available'}
        
        Please provide a 3-4 sentence clinical summary focusing on:
        1. Key health status and risk factors
        2. Notable observations or trends
        3. Potential areas for clinical attention
        4. Overall health assessment
        
        Keep it professional and concise for medical practitioners.
      `;

      const chatCompletion = await client.chatCompletion({
        provider: "novita",
        model: "deepseek-ai/DeepSeek-V3.2-Exp",
        messages: [
          {
            role: "user",
            content: prompt,
          },
        ],
      });

      setAiSummary(chatCompletion.choices[0].message.content);
    } catch (error) {
      console.error('Error generating AI summary:', error);
      setAiSummary("Unable to generate AI summary at this time. Please check your Hugging Face token and try again.");
    } finally {
      setIsGeneratingSummary(false);
    }
  };
  
  // Generate timeline from existing data
  const timelineData = useMemo(() => {
    if (!patient) return [];
    
    const timeline = [];
    
    // Add observations to timeline
    if (patient.observations) {
      patient.observations.forEach(obs => {
        timeline.push({
          date: obs.date,
          type: 'observation',
          observation: `${obs.type}: ${obs.value} ${obs.unit}`,
          medication: obs.medication_context,
          description: `Recorded ${obs.type} measurement`
        });
      });
    }
    
    // Add medication events (simplified - you might have actual start/end dates)
    if (patient.medications) {
      patient.medications.forEach(med => {
        const { status } = getMedicationStatus(med);
        const medName = med.replace(/(active-|completed-|previous-use-)/, '');
        
        timeline.push({
          date: patient.observations?.[0]?.date || patient['D.O.B'] || '2020-01-01', // Fallback date
          type: 'medication',
          medication: med,
          description: `${status} medication: ${medName}`,
          status: status
        });
      });
    }
    
    // Add condition diagnoses (simplified)
    if (patient.conditions) {
      patient.conditions.forEach(condition => {
        const { severity } = getConditionSeverity(condition);
        
        timeline.push({
          date: patient.observations?.[0]?.date || patient['D.O.B'] || '2020-01-01', // Fallback date
          type: 'condition',
          condition: condition,
          description: `Diagnosed with ${condition.toUpperCase()} (${severity})`,
          severity: severity
        });
      });
    }
    
    // Sort by date (most recent first)
    return timeline.sort((a, b) => new Date(b.date) - new Date(a.date));
  }, [patient]);

  if (!patient) {
    return (
      <div className="tc pa4">
        <h2>Patient not found: {id}</h2>
        <Link to="/" className="f4 link blue underline">← Back to Dashboard</Link>
      </div>
    );
  }

  // Generate avatar
  const normalizedGender = patient.Gender?.toString().toLowerCase();
  const seed = `${patient.name}-${patient.Gender}`;
  // const imgSrc = `https://i.pravatar.cc/300?u=${encodeURIComponent(seed)}`;
  const imgSrc = `https://randomuser.me/api/portraits/${
    normalizedGender === 'male' || normalizedGender === 'm' ? 'men' : 'women'
  }/${id % 100}.jpg`;

  const age = patient['D.O.B'] ? calculateAge(patient['D.O.B']) : 'Unknown';

  // Tab content
  const renderOverview = () => (
    <div className="flex flex-wrap">
      {/* Quick Stats */}
      <div className="w-100 mb4">
        <div className="flex justify-around">
          <div className="tc pa3 bg-lightest-blue br3 shadow-1">
            <div className="f2 fw6 blue">{patient.allergies?.length || 0}</div>
            <div className="f6">Allergies</div>
          </div>
          <div className="tc pa3 bg-lightest-green br3 shadow-1">
            <div className="f2 fw6 green">{patient.conditions?.length || 0}</div>
            <div className="f6">Conditions</div>
          </div>
          <div className="tc pa3 bg-lightest-purple br3 shadow-1">
            <div className="f2 fw6 purple">{patient.medications?.length || 0}</div>
            <div className="f6">Medications</div>
          </div>
          <div className="tc pa3 bg-lightest-yellow br3 shadow-1">
            <div className="f2 fw6 gold">{patient.observations?.length || 0}</div>
            <div className="f6">Observations</div>
          </div>
        </div>
      </div>

      {/* Allergies */}
      <div className="w-50 pr2">
        <div className="bg-white br3 pa3 shadow-1">
          <h3 className="f3 mb3 flex items-center">
            <span className="mr2">⚠️</span> Allergies
          </h3>
          {patient.allergies?.length > 0 ? (
            <div className="flex flex-wrap">
              {patient.allergies.map((allergy, index) => (
                <div key={index} className={`w-100 pa2 mb2 br2 ${allergy === 'noneknown' ? 'bg-light-green' : 'bg-light-red'}`}>
                  {allergy === 'noneknown' ? '✅ No Known Allergies' : `❌ ${allergy}`}
                </div>
              ))}
            </div>
          ) : (
            <p className="i gray">No allergy data available</p>
          )}
        </div>
      </div>

      {/* Conditions */}
      <div className="w-50 pl2">
        <div className="bg-white br3 pa3 shadow-1">
          <h3 className="f3 mb3 flex items-center">
            <span className="mr2">🏥</span> Medical Conditions
          </h3>
          {patient.conditions?.length > 0 ? (
            <div>
              {patient.conditions.map((condition, index) => {
                const { severity, color } = getConditionSeverity(condition);
                return (
                  <div key={index} className="flex items-center justify-between mb2 pv2 ph2 bg-white br2 shadow-1">
                    <span>{condition.toUpperCase()}</span>
                    <span className={`pa1 br2 white bg-${color}`} style={{ fontSize: '0.7rem' }}>
                      {severity}
                    </span>
                  </div>
                );
              })}
            </div>
          ) : (
            <p className="i gray">No condition data available</p>
          )}
        </div>
      </div>
    </div>
  );

  const renderMedications = () => (
    <div className="bg-white br3 pa3 shadow-1">
      <h3 className="f3 mb3 flex items-center">
        <span className="mr2">💊</span> Medications
      </h3>
      {patient.medications?.length > 0 ? (
        <div className="flex flex-wrap">
          {patient.medications.map((medication, index) => {
            const { status, color } = getMedicationStatus(medication);
            const medName = medication.replace(/(active-|completed-|previous-use-)/, '');
            return (
              <div key={index} className="w-50 pa2">
                <div className="bg-white br3 pa3 shadow-1 h-100">
                  <div className="flex justify-between items-center mb2">
                    <strong className="f5">{medName}</strong>
                    <span className={`pa1 br2 white bg-${color}`} style={{ fontSize: '0.7rem' }}>
                      {status}
                    </span>
                  </div>
                  <div className="f6 gray">
                    {status === 'Active' && '🟢 Currently taking'}
                    {status === 'Completed' && '🔵 Treatment completed'}
                    {status === 'Previous' && '⚪ Previously used'}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      ) : (
        <p className="i gray">No medication data available</p>
      )}
    </div>
  );

  const renderObservations = () => (
    <div className="bg-white br3 pa3 shadow-1">
      <h3 className="f3 mb3 flex items-center">
        <span className="mr2">🔬</span> Clinical Observations
      </h3>
      {patient.observations?.length > 0 ? (
        <div className="flex flex-wrap">
          {patient.observations.map((observation, index) => (
            <div key={index} className="w-33 pa2">
              <div className="bg-lightest-blue br3 pa3 tc h-100">
                <div className="f6 gray mb1">{observation.type}</div>
                <div className="f4 b mb2" style={{ color: '#2d3748' }}>
                  {observation.value} {observation.unit}
                </div>
                <div className="f7 gray mb1">
                  Ref: {observation.reference_range}
                </div>
                <div className="f7 gray mb1">
                  Date: {observation.date}
                </div>
                <div className="f7 gray">
                  Context: {observation.medication_context}
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <p className="i gray">No observation data available</p>
      )}
    </div>
  );

  const renderTimeline = () => (
    <div className="bg-white br3 pa3 shadow-1">
      <h3 className="f3 mb3 flex items-center">
        <span className="mr2">📅</span> Medical Timeline
      </h3>
      {timelineData.length > 0 ? (
        <div className="timeline">
          {timelineData.map((event, index) => (
            <div key={index} className="flex items-start mb4">
              {/* Timeline line and dot */}
              <div className="flex flex-column items-center mr3">
                <div className={`w2 h2 br-100 ${
                  event.type === 'observation' ? 'bg-blue' : 
                  event.type === 'medication' ? 'bg-green' : 
                  'bg-orange'
                }`}></div>
                {index < timelineData.length - 1 && (
                  <div className="w1 h4 bg-gray" style={{ minHeight: '2rem' }}></div>
                )}
              </div>
              
              {/* Event content */}
              <div className="flex-auto bg-lightest-blue br3 pa3">
                <div className="flex justify-between items-center mb2">
                  <div className="f5 b">{event.date}</div>
                  <div className={`pa1 br2 white f7 ${
                    event.type === 'observation' ? 'bg-blue' : 
                    event.type === 'medication' ? 'bg-green' : 
                    'bg-orange'
                  }`}>
                    {event.type.toUpperCase()}
                  </div>
                </div>
                
                <div className="f5 mb2">{event.description}</div>
                
                <div className="flex flex-wrap f6 gray">
                  {event.observation && (
                    <span className="mr3">🩺 {event.observation}</span>
                  )}
                  {event.medication && (
                    <span className="mr3">💊 {event.medication.replace(/(active-|completed-|previous-use-)/, '')}</span>
                  )}
                  {event.condition && (
                    <span className="mr3">🏥 {event.condition.toUpperCase()}</span>
                  )}
                  {event.status && (
                    <span className={`mr3 ${
                      event.status === 'Active' ? 'green' : 
                      event.status === 'Completed' ? 'blue' : 
                      'gray'
                    }`}>
                      {event.status}
                    </span>
                  )}
                  {event.severity && (
                    <span className={`${
                      event.severity === 'Severe' ? 'red' : 
                      event.severity === 'Moderate' ? 'orange' : 
                      'green'
                    }`}>
                      {event.severity}
                    </span>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <p className="i gray">No timeline data available</p>
      )}
    </div>
  );

  return (
    <div className="pa4" style={{ background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)', minHeight: '100vh' }}>
      <Link to="/" className="f4 link white underline mb4 db">← Back to Dashboard</Link>
      
      {/* Patient Header */}
      <div className="flex items-center mb4 bg-white br3 pa3 shadow-2">
        <img 
          alt="profile" 
          src={imgSrc} 
          className="br-100 h4 w4 dib ba b--black-05 pa2 shadow-1" 
        />
        <div className="pl3 flex-auto">
          <h1 className="f1 mb2" style={{ color: '#2d3748' }}>{patient.name}</h1>
          <div className="flex flex-wrap">
            <div className="mr4 mb2">
              <strong>Gender:</strong> {patient.Gender} {patient.Gender === 'male' ? '👨' : patient.Gender === 'female' ? '👩' : '⚧'}
            </div>
            <div className="mr4 mb2">
              <strong>Age:</strong> {age} years
            </div>
            <div className="mr4 mb2">
              <strong>Date of Birth:</strong> {patient['D.O.B']}
            </div>
            <div className="mb2">
              <strong>Patient ID:</strong> {patient.id}
            </div>
          </div>
        </div>
        
        {/* AI Summary Button */}
        <button
          onClick={generatePatientSummary}
          disabled={isGeneratingSummary}
          className={`f5 ph3 pv2 br2 bn white ${
            isGeneratingSummary ? 'bg-gray' : 'bg-green'
          }`}
        >
          {isGeneratingSummary ? '🔄 Generating...' : '🤖 AI Summary'}
        </button>
      </div>

      {/* AI Summary Modal */}
      {showAiSummary && (
        <div className="fixed top-0 left-0 w-100 h-100 bg-black-70 flex items-center justify-center z-999">
          <div className="bg-white br3 pa4 mw6 w-90 shadow-3">
            <div className="flex justify-between items-center mb3">
              <h3 className="f3 ma0">🤖 AI Patient Summary</h3>
              <button
                onClick={() => setShowAiSummary(false)}
                className="f4 bn bg-transparent gray pointer"
              >
                ×
              </button>
            </div>
            {isGeneratingSummary ? (
              <div className="tc pv4">
                <div className="f4 mb3">Generating AI summary...</div>
                <div className="spinner"></div>
                <div className="f6 gray mt3">This may take a few moments</div>
              </div>
            ) : (
              <div>
                <div className="f5 lh-copy mb4" style={{ whiteSpace: 'pre-wrap' }}>
                  {aiSummary}
                </div>
                <button
                  onClick={() => setShowAiSummary(false)}
                  className="f5 ph3 pv2 br2 bn bg-blue white pointer w-100"
                >
                  Close Summary
                </button>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Navigation Tabs */}
      <div className="flex mb4 bg-white br3 shadow-1">
        {['overview', 'medications', 'observations', 'timeline'].map(tab => (
          <button
            key={tab}
            className={`f5 pa3 flex-auto br2 bn ${activeTab === tab ? 'bg-blue white' : 'bg-white'}`}
            onClick={() => setActiveTab(tab)}
          >
            {tab.charAt(0).toUpperCase() + tab.slice(1)}
          </button>
        ))}
      </div>

      {/* Tab Content */}
      <div className="bg-transparent">
        {activeTab === 'overview' && renderOverview()}
        {activeTab === 'medications' && renderMedications()}
        {activeTab === 'observations' && renderObservations()}
        {activeTab === 'timeline' && renderTimeline()}
      </div>
    </div>
  );
};

export default PatientDetail;